.services {
	position: relative;
	z-index: 10;
	&__form {
		padding: 65px 138px 51px;
		background: rgba($blue, 0.4);
		@include md {
			padding: 48px 165px 52px; }
		@include sm {
			padding: 0;
			background: none; } }
	&__title {
		margin-bottom: 12px;
		@include md {
			margin-bottom: 17px; } }
	&__subtitle {
		margin: 0 auto 45px;
		max-width: 730px;
		@include f('ll', 22, 36);
		text-align: center;
		@include md {
			margin-bottom: 30px;
			max-width: 550px;
			@include f(null, 17, 28); } }
	&__row {
		@include clr; }
	&__col {
		float: left;
		padding: 0 5px 10px;
		width: 100%;
		@include md {
			padding: 0 5px 8px; }
		&_sm {
			width: 33.33%;
			@include md {
				width: 50%; }
			@include xs {
				width: 100%; }
			&:nth-child(1) {
				@include md {
					width: 100%; } } } }
	&__submit {
		padding: 10px 10px 0;
		text-align: center;
		@include md {
			padding-top: 24px; } }
	&__container {
		position: relative; }
	&__error {
		position: absolute;
		top: 0;
		left: 100%;
		padding: 6px 0 0 20px;
		margin-left: 5px;
		width: 95px;
		color: $red;
		@include f('lli', 15, 20);
		@include sm {
			display: none; } } }

.form-error {
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 13px;
		height: 30px;
		background: url(../img/error-arrow.svg) no-repeat center center;
		background-size: contain; }
	strong {
		display: none; }
	li {
		&:not(:first-child) {
			display: none; } } }
