.counters {
	position: relative;
	z-index: 10;
	margin: 40px 0;
	@include md {
		margin: 0 0 5px; }
	@include sm {
		margin-bottom: 0; }
	&__heading {
		margin-bottom: 75px;
		@include md {
			margin-bottom: 65px; }
		@include sm {
			margin-bottom: 25px; } }
	&__row {
		margin: 0 -10px 20px;
		@include clr;
		@include md {
			margin: 0 -10px; }
		@include xs {
			margin-bottom: 20px; } }
	&__col {
		float: left;
		padding: 0 10px 50px;
		width: 25%;
		@include md {
			padding-bottom: 50px; }
		@include sm {
			padding: 40px 10px;
			width: 50%; }
		@include xs {
			padding: 40px 10px 20px;
			width: 100%; } }
	&__btn {
		text-align: center;
		@include md {
			display: none; } } }

.counter {
	text-align: center;
	&__numbers {
		margin-bottom: 20px;
		color: $yellow;
		@include f('lt', 100, 72);
		text-transform: uppercase; }
	&__description {
		margin: 0 auto;
		max-width: 230px;
		color: $white;
		@include f('ll', 24, 30);
		@include md {
			max-width: 190px;
			@include f(null, 20, 24); }
		strong {
			@include f('black'); } } }
